@import "./variables";

.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;

    &:hover {
      transition: transform $trans-delay, color $trans-delay;
      color: $title-name-hov-col;
      transform: scale($transf-size);
    }
  }

  &__card:not(:nth-child(4n)) {
    margin-right: 48px;
  }
}
